import axios from "axios";
import React, { Component, Fragment } from "react";
import { MdArrowBack, MdEdit } from "react-icons/md";
import { VscLoading } from "react-icons/vsc";
import { connect } from "react-redux";
import { Auth, PushPopupAlert } from "../../../actions";
import {
  GetPlansTypes,
  setSelectedPlanType,
  GetAllAcademicSession,
  createAcademicSession,
  updateAcademicSession,
  deleteAcademicSession,
  academicSessionActivation,
} from "../../../actions/session";
import ActionConfirm, {
  ActionConfirmEnum,
} from "../../../components/ActionConfirm/ActionConfirm";
import { PopupAlertData } from "../../../components/Alert/Popup";
import TableLoading from "../../../components/Loading/TableLoading";
import NoResult, {
  NoResultThemes,
} from "../../../components/NoResult/NoResult";
import { AcademicSessionInterface } from "../../../interfaces";
import { PopupAlertTheme } from "../../../interfaces/popupAlert";
import { StoreState } from "../../../reducers";
import { SessionPlanReducerInterface } from "../../../reducers/session";
import {
  capitalizeFirstLetter,
  isEmptyOrSpaces,
} from "../../../shared/dataTest";
import { API } from "../../../utils/api";
import { errorToText } from "../../../utils/functions";
// import FormSteps from "./FormSteps";

interface AcademicSessionProps {
  isCreateDefault: boolean;

  //   REDUX ############################
  auth: Auth;
  sessionPlan: SessionPlanReducerInterface;
  GetPlansTypes: Function;
  setSelectedPlanType: Function;
  GetAllAcademicSession: Function;
  createAcademicSession: Function;
  updateAcademicSession: Function;
  academicSessionActivation: Function;
  deleteAcademicSession: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface AcademicSessionState {
  loading: boolean;
  createSession: boolean;
  loadingRequest: string;
  activateSession: AcademicSessionInterface | null;
  thisSession: AcademicSessionInterface | null;
  doAction: "ACTIVATED" | "EDIT" | "DELETE" | null;
  sessionTitle: string;
  formError: {
    element: string;
    msg: string;
  } | null;
  // currentTab: "ACTIVE" | "DELETED";
}

export class AcademicSession extends Component<
  AcademicSessionProps,
  AcademicSessionState
> {
  constructor(props: AcademicSessionProps) {
    super(props);

    this.state = {
      loading: false,
      loadingRequest: "",
      createSession: false,
      activateSession: null,
      thisSession: null,
      doAction: null,
      sessionTitle: "",
      formError: null,
      // currentTab: "ACTIVE",
    };
  }

  setLoading = (state: boolean) => {
    this.setState({ loading: state });
  };

  setCreateSession = (state: boolean) => {
    this.setState({ createSession: state });
  };

  setSessionTitle = (data: string) => {
    this.setState({ sessionTitle: data });
  };

  setActivateSession = (data: AcademicSessionInterface | null) => {
    this.setState({ activateSession: data });
  };

  setFormError = (
    data: {
      element: string;
      msg: string;
    } | null
  ) => {
    this.setState({ formError: data });
  };

  componentDidMount = () => {
    if (
      this.props.sessionPlan.academicSessions === null ||
      this.props.sessionPlan.academicSessions.length <= 0
    ) {
      this.props.GetAllAcademicSession((status: boolean, msg: string) => {
        this.setLoading(status);
        if (!status && msg.length > 0) {
          this.props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    } else this.setLoading(false);

    if (!this.state.createSession) {
      this.setCreateSession(this.props.isCreateDefault);
    }
  };

  handleSessionActivation = async (session: AcademicSessionInterface) => {
    const newState: "activate" | "deactivate" = session.is_active
      ? "deactivate"
      : "activate";

    this.setState({ loadingRequest: session._id });

    try {
      const res = await axios.patch<AcademicSessionInterface>(
        `${API.PLACEMENT}/postrequestperiod/openorclose/${session._id}`,
        {
          value: !session.is_active,
        }
      );
      // this.props.academicSessionActivation(res.data);
      this.props.GetAllAcademicSession((status: boolean, msg: string) => {
        // this.setLoading(status);
        if (!status && msg.length > 0) {
          this.props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
      this.props.PushPopupAlert({
        title: `Session ${newState}d successfully`,
        theme: PopupAlertTheme.info,
      });
      console.log("updateAcademicSession: ", res);
    } catch (error) {
      console.error({ error: errorToText(error) });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.warning,
      });
    }

    this.setState({ loadingRequest: "" });
  };

  handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    // validation
    if (isEmptyOrSpaces(this.state.sessionTitle)) {
      return this.setFormError({
        element: "title",
        msg: "Please fill the session plan title",
      });
    }

    console.log("====================================");
    console.log({ title: this.state.sessionTitle });
    console.log("====================================");

    this.setState({
      loadingRequest: this.state.thisSession
        ? this.state.thisSession._id
        : "true",
    });

    try {
      // Update current
      if (this.state.thisSession !== null) {
        const res = await axios.patch<{
          msg: string;
          data: AcademicSessionInterface;
        }>(`${API.PLACEMENT}/postrequestperiod/${this.state.thisSession._id}`, {
          title: this.state.sessionTitle,
          // is_active: this.state.thisSession.is_active,
        });
        console.log("====================================");
        console.log({ updateAcademicSession: res });
        console.log("====================================");
        this.props.updateAcademicSession(res.data.data);
        this.props.PushPopupAlert({
          title: `Academic session updated successfully`,
          theme: PopupAlertTheme.info,
        });
        this.setState({
          formError: null,
          sessionTitle: "",
          thisSession: null,
          createSession: false,
        });
        console.log("updateAcademicSession: ", res);
      } else {
        // Add new
        const res = await axios.post<any>(
          `${API.PLACEMENT}/postrequestperiod`,
          {
            title: this.state.sessionTitle,
          }
        );
        this.props.createAcademicSession(res.data);
        this.props.PushPopupAlert({
          title: `Academic session created successfully`,
          theme: PopupAlertTheme.success,
        });
        this.setState({
          formError: null,
          sessionTitle: "",
        });
        console.log("createAcademicSessionType: ", res);
      }
    } catch (error) {
      this.props.PushPopupAlert({
        title: `${errorToText(error)}! Try again`,
        theme: PopupAlertTheme.danger,
      });
      console.error("Err: ", { error });
    }
    this.setState({ loadingRequest: "" });
  };

  render() {
    return (
      <div className="my-2">
        {/* HEADER TITLE ===================================== */}
        <div className="flex flex-row justify-between items-center bg-gray-100 py-1">
          {this.state.createSession && (
            <div className="flex items-center gap-2">
              <button
                className={`ml-3 bg-gray-50 hover:bg-blue-100 rounded-full px-1 py-1 focus:outline-none ${
                  this.state.loadingRequest.length > 0
                    ? true
                    : false
                    ? "cursor-not-allowed"
                    : "cursor-pointer"
                } animate__animated animate__fadeIn`}
                disabled={this.state.loadingRequest.length > 0 ? true : false}
                onClick={() => {
                  this.setState({
                    formError: null,
                    sessionTitle: "",
                    thisSession: null,
                    createSession: false,
                  });
                }}
              >
                <MdArrowBack className="w-7 h-7" />
              </button>

              <div className="font-extrabold text-2xl text-gray-500">
                {this.state.thisSession !== null
                  ? `Updating ${this.state.thisSession.title} Session`
                  : "Add New Academic Session"}
              </div>
            </div>
          )}
          {!this.state.createSession && (
            <div className="text-blue-600 font-extrabold text-xl px-2">
              Academic Sessions
            </div>
          )}
          {this.state.loading === false && !this.state.createSession && (
            <div
              onClick={() => this.setCreateSession(true)}
              className="btn bg-blue-600 text-white cursor-pointer hover:bg-blue-800"
            >
              Add new
            </div>
          )}
        </div>
        {/* END HEADER TITLE ===================================== */}
        {/* ###################################################################### */}
        {/* CONTENT ===================================== */}
        {!this.state.createSession && (
          <div className="flex flex-col my-2">
            <table className="w-full shadow" id="session_content">
              <thead className="bg-white border-b-2 border-gray-300 text-gray-600 font-bold text-sm">
                <tr>
                  <td className="px-2 py-2">#</td>
                  <td className="px-2 py-2">Session title</td>
                  <td className="px-2 py-2">Status</td>
                  <td className="px-2 py-2"></td>
                </tr>
              </thead>
              <tbody>
                {/* WHILE STILL LOADING ====================== */}
                {this.props.sessionPlan.academicSessions === null && (
                  <TableLoading cols={4} />
                )}
                {/* WHILE NO DATA FOUND ====================== */}
                {this.props.sessionPlan.academicSessions !== null &&
                  !this.state.loading &&
                  this.props.sessionPlan.academicSessions.length <= 0 && (
                    <tr>
                      <td colSpan={4} className="px-2 py-2">
                        <NoResult
                          theme={NoResultThemes.Dark}
                          value={"No academic session found!"}
                          component={
                            <div className="w-full mt-2 flex flex-row items-center justify-center">
                              <ul className="text-base py-2 text-blue-600 font-bold flex flex-row gap-2">
                                <li
                                  onClick={() => this.setCreateSession(true)}
                                  className="underline text-blue-600 cursor-pointer"
                                >
                                  Create new session
                                </li>
                              </ul>
                            </div>
                          }
                        />
                      </td>
                    </tr>
                  )}
                {/* WHILE NO DATA FOUND ====================== */}
                {this.props.sessionPlan.academicSessions !== null &&
                  !this.state.loading &&
                  this.props.sessionPlan.academicSessions.length > 0 &&
                  this.props.sessionPlan.academicSessions.map((item, index) => (
                    <Fragment key={index + item._id}>
                      <tr
                        className={`border-b border-gray-200 hover:bg-gray-100 group`}
                      >
                        <td className="p-3 text-left whitespace-nowrap">
                          {index + 1}
                        </td>
                        <td className="p-3 text-left">
                          <span className="text-lg">
                            {capitalizeFirstLetter(item.title)}
                          </span>
                        </td>
                        <td>
                          {item.is_active ? (
                            <span
                              className={`px-2 py-1 font-semibold text-green-700`}
                            >
                              Active
                            </span>
                          ) : (
                            <span
                              className={`px-2 py-1 font-semibold text-red-700`}
                            >
                              Inactive
                            </span>
                          )}
                        </td>
                        <td>
                          <div className="flex items-center justify-center gap-1">
                            <div
                              className={`relative ${
                                this.state.loadingRequest.length > 0 ||
                                item.is_active
                                  ? "cursor-not-allowed"
                                  : "cursor-pointer"
                              }`}
                              title={
                                item.is_active
                                  ? "Activate another session to close this one!"
                                  : "Activation of a session"
                              }
                              onClick={() =>
                                this.state.loadingRequest.length <= 0 &&
                                !item.is_active &&
                                this.setActivateSession(item)
                              }
                            >
                              <span className="block w-14 h-8 bg-gray-100 rounded-full shadow-inner"></span>
                              <span
                                className={`absolute block w-6 h-6 mt-1 ml-1 rounded-full shadow inset-y-0 left-0 focus-within:shadow-outline transition-transform duration-500 ease-in-out ${
                                  !item.is_active
                                    ? "bg-red-500"
                                    : "bg-green-500 transform translate-x-full"
                                }`}
                              ></span>
                            </div>
                            {this.state.loadingRequest === item._id &&
                              this.state.doAction &&
                              this.state.doAction === "ACTIVATED" && (
                                <VscLoading className="animate__animated animate__rotateIn animate__infinite" />
                              )}
                          </div>
                        </td>
                        <td className="px-5 text-center">
                          <div className="flex flex-row items-center gap-2 justify-end">
                            <div
                              onClick={() => {
                                this.state.loadingRequest.length <= 0 &&
                                  this.setState({
                                    thisSession: item,
                                    createSession: true,
                                    sessionTitle: item.title,
                                  });
                              }}
                              className={`bg-gray-50 group-hover:bg-blue-200 rounded-full px-1 py-1 ${
                                this.state.loadingRequest.length > 0
                                  ? "cursor-not-allowed"
                                  : "cursor-pointer"
                              }`}
                            >
                              <MdEdit className="text-3xl text-blue-700" />
                            </div>
                            {/* <div
                                    // onClick={() => this.deletePlanType(item)}
                                    className={`bg-gray-50 group-hover:bg-red-200 flex items-center rounded-full px-1 py-1 ${
                                      this.state.loadingRequest.length > 0
                                        ? "cursor-not-allowed"
                                        : "cursor-pointer"
                                    }`}
                                  >
                                    <IoMdTrash className="text-3xl text-yellow-700" />
                                    {this.state.loadingRequest ===
                                      item._id &&
                                      this.state.doAction &&
                                      this.state.doAction === "DELETE" && (
                                        <VscLoading className="animate__animated animate__rotateIn animate__infinite" />
                                      )}
                                  </div> */}
                          </div>
                        </td>
                      </tr>
                    </Fragment>
                  ))}
              </tbody>
            </table>
          </div>
        )}
        {/* END CONTENT ===================================== */}
        {/* PLAN TYPES FORM =========================================== */}
        {this.state.createSession && (
          <section className="grid grid-cols-1 sm:grid-cols-4 gap-4">
            {/* TESTING ================== */}
            {/* <FormSteps /> */}
            <div className="col-span-1 sm:col-span-4 flex flex-col mx-4 my-6">
              <form onSubmit={this.handleSubmit} className="-mt-2">
                <div className="flex flex-col mb-2">
                  <span className="text-sm text-gray-600 font-bold mb-1">
                    Session title
                  </span>
                  <input
                    disabled={
                      this.state.loadingRequest.length > 0 ? true : false
                    }
                    onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                      this.setSessionTitle(e.target.value);
                      this.setFormError(null);
                    }}
                    type="text"
                    value={this.state.sessionTitle}
                    className={`input-white border-2 ${
                      this.state.formError !== null &&
                      this.state.formError.element === "title"
                        ? "border-red-500"
                        : "border-gray-500"
                    }`}
                  />
                  {this.state.formError !== null &&
                    this.state.formError.element === "title" && (
                      <span className="text-red-600 font-bold text-xs">
                        {this.state.formError.msg}
                      </span>
                    )}
                </div>

                <div className="flex flex-row justify-between items-center">
                  <button
                    disabled={
                      this.state.loadingRequest.length > 0 ? true : false
                    }
                    type="button"
                    className={`btn bg-gray-500 hover:bg-gray-700 text-white`}
                    onClick={() => {
                      this.setState({
                        formError: null,
                        sessionTitle: this.state.thisSession
                          ? this.state.thisSession.title
                          : "",
                      });
                    }}
                  >
                    Cancel
                  </button>

                  <button
                    disabled={
                      this.state.loadingRequest.length > 0 ? true : false
                    }
                    type="submit"
                    className={`btn btn-blue text-white ${
                      this.state.loadingRequest.length > 0
                        ? "animate__animated animate__flash animate__infinite cursor-wait"
                        : ""
                    }`}
                  >
                    {this.state.loadingRequest.length > 0
                      ? "Please wait..."
                      : this.state.thisSession !== null
                      ? "Update"
                      : "Create"}
                  </button>
                </div>
              </form>
            </div>
          </section>
        )}

        {this.state.activateSession !== null && (
          <ActionConfirm
            modalType={ActionConfirmEnum.OTHER}
            confirmFun={() =>
              this.handleSessionActivation(this.state.activateSession!)
            }
            setModalAction={(status: boolean) =>
              !status && this.setActivateSession(null)
            }
            description={
              "Do you really want to activate this session? This process will affect post requests!"
            }
          />
        )}
      </div>
    );
  }
}

const mapStateToProps = ({
  auth,
  sessionPlan,
}: StoreState): {
  auth: Auth;
  sessionPlan: SessionPlanReducerInterface;
} => {
  return {
    auth,
    sessionPlan,
  };
};

export default React.memo(
  connect(mapStateToProps, {
    GetPlansTypes,
    setSelectedPlanType,
    GetAllAcademicSession,
    createAcademicSession,
    updateAcademicSession,
    academicSessionActivation,
    deleteAcademicSession,
    PushPopupAlert,
  })(AcademicSession)
);
