import React, { useState } from "react";
import { RiFolderSettingsFill } from "react-icons/ri";
import { NavLink, Redirect, useParams } from "react-router-dom";
import CalendarView from "./CalendarView";
import ApplicationSession from "./ApplicationSession/ApplicationSession";
import AcademicSession from "./AcademicSession/AcademicSession";
import { connect } from "react-redux";
import { Auth, FC_Access } from "../../actions";
import { StoreState } from "../../reducers";
import { isEmptyOrSpaces } from "../../shared/dataTest";
import { IoWarning } from "react-icons/io5";
import { UserAccessList } from "../../utils/auth_access";

enum sessionSettingsTabs {
  applicationSession = "Application Session",
  academicSession = "Academic Session",
  calenderView = "Calendar View",
}

type IndexProps = {
  auth: Auth;
};

const SessionIndex: React.FC<IndexProps> = (props) => {
  const { setting_type } = useParams<{ setting_type: string }>();
  const [selectedTab, setSelectedTab] = useState<sessionSettingsTabs | null>(
    setting_type === "calendar"
      ? sessionSettingsTabs.calenderView
      : setting_type === "academic"
      ? sessionSettingsTabs.academicSession
      : sessionSettingsTabs.applicationSession
  );

  if (FC_Access(props.auth, UserAccessList.SYSTEM_SETTINGS).view === false) {
    return <Redirect to="/apps-page" />;
  }

  if (
    props.auth.education_institution_id === null ||
    isEmptyOrSpaces(props.auth.education_institution_id)
  )
    return (
      <div className="w-full my-5">
        <section className="py-10 flex flex-col items-center rounded border border-gray-300 bg-white text-2xl font-medium text-yellow-800 animate__animated animate__pulse">
          <div className="flex flex-col items-center">
            <IoWarning className="w-32 h-32" />
            You have no access to this page!
          </div>
          <div className="text-center group text-sm -mb-3 mt-5">
            <NavLink
              to="/apps-page"
              className="border border-rounded-md bg-gray-100 text-gray-900 rounded border-gray-100 group-hover:bg-gray-50 group-hover:border-gray-400 px-2 py-1"
            >
              Back to App
            </NavLink>
          </div>
        </section>
      </div>
    );

  return (
    <div className="">
      <div className="mt-3 rounded flex flex-row justify-between items-center">
        <div className="flex flex-row items-center gap-3">
          <RiFolderSettingsFill className="text-6xl text-blue-600" />
          <div className="flex flex-col">
            <span className="text-blue-600 text-2xl font-bold">
              Session settings
            </span>
            {props.auth.education_institution_name && (
              <div>
                <span className="bg-blue-500 px-2 py-1 text-blue-50 rounded-md">
                  {props.auth.education_institution_name}
                </span>
              </div>
            )}
          </div>
        </div>
      </div>
      <div className="mt-3 mb-6 flex-col">
        <div className="bg-white w-full border border-white rounded">
          <div className="flex flex-row items-center">
            {Object.values(sessionSettingsTabs).map((item, i) => (
              <div
                key={i}
                onClick={() => setSelectedTab(item)}
                className={`animate__animated ${
                  selectedTab === item
                    ? "bg-blue-100 border-blue-600 text-blue-600 animate__zoomIn"
                    : "bg-white border-white text-gray-600"
                } hover:bg-blue-100 px-4 py-2 cursor-pointer font-bold border-b-4 hover:border-blue-600 hover:text-blue-600 rounded-t mt-1 mx-1`}
              >
                {item}
              </div>
            ))}
          </div>
          <div className="=rounded mx-1 mb-1">
            {selectedTab === sessionSettingsTabs.applicationSession && (
              <ApplicationSession
                isCreateDefault={setting_type === "create-session"}
              />
            )}
            {selectedTab === sessionSettingsTabs.academicSession && (
              <AcademicSession
                isCreateDefault={setting_type === "create-session"}
              />
            )}
            {selectedTab === sessionSettingsTabs.calenderView && (
              <CalendarView />
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

const mapStateToProps = ({
  auth,
}: StoreState): {
  auth: Auth;
} => {
  return {
    auth,
  };
};
export const Index = connect(mapStateToProps)(SessionIndex);
