import React, { Component } from "react";
import { connect } from "react-redux";
import { GetAllSessionPlans } from "../../actions/session";
import { StoreState } from "../../reducers";
import { SessionPlanReducerInterface } from "../../reducers/session";
import { Calendar, View, DateLocalizer } from "react-big-calendar";
import { momentLocalizer } from "react-big-calendar";
import "react-big-calendar/lib/css/react-big-calendar.css";
import moment from "moment";
import { PushPopupAlert } from "../../actions";
import { PopupAlertData } from "../../components/Alert/Popup";
import { PopupAlertTheme } from "../../interfaces/popupAlert";
import { PlansTypeKeys, SessionPlanInterface } from "../../interfaces";

type eventsInterface = {
  id: number;
  title: string;
  allDay?: boolean;
  start: Date;
  end: Date;
  desc?: string;
};

interface CalendarViewProps {
  //   REDUX ############################
  sessionPlan: SessionPlanReducerInterface;
  GetAllSessionPlans: Function;
  PushPopupAlert: (alert: PopupAlertData) => void;
}

interface CalendarViewState {
  loading: boolean;
  events: eventsInterface[] | null;
}

const allViews: View[] = ["month", "agenda"];
const localizer: DateLocalizer = momentLocalizer(moment);

class CalendarView extends Component<CalendarViewProps, CalendarViewState> {
  constructor(props: CalendarViewProps) {
    super(props);

    this.state = { loading: true, events: null };
  }

  setLoading = (state: boolean) => {
    this.setState({ loading: state });
  };

  loadEvents = (allSession: SessionPlanInterface[]) => {
    let allEvents: eventsInterface[] = [],
      n: number = 1;
    for (const key in allSession) {
      if (Object.prototype.hasOwnProperty.call(allSession, key)) {
        const thisSession = allSession[key];
        for (const plan_key in thisSession.plans) {
          if (
            Object.prototype.hasOwnProperty.call(thisSession.plans, plan_key)
          ) {
            const thisPlan = thisSession.plans[plan_key];
            const thisTitle: string =
              thisPlan.type_key === PlansTypeKeys.APPLICATION
                ? thisSession.session_plan_name + " - Applications"
                : thisPlan.type_key === PlansTypeKeys.DOCUMENT_VALIDATION
                ? thisSession.session_plan_name + " - Document validation"
                : thisPlan.type_key === PlansTypeKeys.EXAMINATION
                ? thisSession.session_plan_name + " - Examination"
                : thisPlan.type_key === PlansTypeKeys.MARKS_PUBLICATION
                ? thisSession.session_plan_name + " - Marks publication"
                : thisPlan.type_key === PlansTypeKeys.PLACEMENT_TRANSFER
                ? thisSession.session_plan_name + " - Placement transfer"
                : thisSession.session_plan_name;
            allEvents.push({
              id: n,
              title: thisTitle,
              allDay: true,
              start: moment(thisPlan.start_date).toDate(),
              end: moment(thisPlan.end_date).toDate(),
            });
            n++;
          }
        }
      }
    }
    this.setState({ events: allEvents });
    n = 1;
    allEvents = [];
  };

  componentDidMount = () => {
    if (
      this.props.sessionPlan.allSessionPlans === null ||
      this.props.sessionPlan.allSessionPlans.length <= 0
    ) {
      this.props.GetAllSessionPlans(
        (status: boolean, msg: string, data: SessionPlanInterface[]) => {
          this.setLoading(status);
          if (!status && data) {
            this.loadEvents(data);
          }
          if (!status && msg.length > 0) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
    } else {
      this.loadEvents(this.props.sessionPlan.allSessionPlans);
      this.setLoading(false);
    }
  };

  render() {
    return (
      <section className="my-3">
        {this.state.events && (
          <Calendar
            popup
            localizer={localizer}
            events={this.state.events}
            defaultDate={new Date()}
            views={allViews}
            step={60}
            showMultiDayTimes
            onSelectEvent={(event) => alert(event.title)}
            startAccessor="start"
            endAccessor="end"
            className="min-h-screen"
            // style={{ height: "100vh" }}
          />
        )}
      </section>
    );
  }
}

const mapStateToProps = ({
  sessionPlan,
}: StoreState): {
  sessionPlan: SessionPlanReducerInterface;
} => {
  return {
    sessionPlan,
  };
};

export default React.memo(
  connect(mapStateToProps, {
    GetAllSessionPlans,
    PushPopupAlert,
  })(CalendarView)
);
