  componentDidMount = () => {
    if (
      this.props.sessionPlan.planTypes === null ||
      this.props.sessionPlan.planTypes.length <= 0
    ) {
      this.props.GetPlansTypes((status: boolean, msg: string) => {
        this.setLoading(status);
        if (!status && msg.length > 0) {
          this.props.PushPopupAlert({
            title: msg,
            theme: PopupAlertTheme.danger,
          });
        }
      });
    }

    if (
      this.props.sessionPlan.planTypes !== null &&
      this.props.sessionPlan.planTypes.length > 0
    ) {
      this.setLoading(false);
    }

    if (
      (this.props.sessionPlan.allSessionPlans === null ||
        this.props.sessionPlan.allSessionPlans.length <= 0) &&
      this.props.auth.education_institution_id
    ) {
      this.props.FC_GetAllSessionByInstitution(
        this.props.auth.education_institution_id,
        (status: boolean, msg: string) => {
          this.setLoading(status);
          if (!status && msg.length > 0) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
    }

    if (!this.state.createSession) {
      this.setCreateSession(this.props.isCreateDefault);
    }
  };

  handleSessionActivation = async (session: SessionPlanInterface) => {
    const newState: "activate" | "deactivate" = session.is_active
      ? "deactivate"
      : "activate";

    if (this.props.auth.education_institution_id === null)
      return this.props.PushPopupAlert({
        title: "You have no access!",
        theme: PopupAlertTheme.warning,
      });

    this.setState({ loadingRequest: session._id });

    try {
      const res = await axios.put<SessionPlanInterface>(
        `${API.SESSION}/session/active/${session._id}`,
        {
          is_active: !session.is_active,
        }
      );
      // this.props.sessionActivation(res.data);
      this.props.FC_GetAllSessionByInstitution(
        this.props.auth.education_institution_id,
        (status: boolean, msg: string) => {
          // this.setLoading(status);
          if (!status && msg.length > 0) {
            this.props.PushPopupAlert({
              title: msg,
              theme: PopupAlertTheme.danger,
            });
          }
        }
      );
      this.props.PushPopupAlert({
        title: `Session ${newState}d successfully`,
        theme: PopupAlertTheme.info,
      });
      console.log("updateSessionPlan: ", res);
    } catch (error) {
      console.error({ error: errorToText(error) });
      this.props.PushPopupAlert({
        title: errorToText(error),
        theme: PopupAlertTheme.warning,
      });
    }

    this.setState({ loadingRequest: "" });
  };

