  currentDate = (): string => {
    let today = new Date();
    let dd = today.getDate();
    let mm = today.getMonth() + 1; //January is 0!
    let yyyy = today.getFullYear(),
      date: string = dd.toString(),
      month: string = mm.toString();

    if (dd < 10) {
      date = "0" + dd;
    }

    if (mm < 10) {
      month = "0" + mm;
    }

    return yyyy + "-" + month + "-" + date;
  };

